firebase.initializeApp(firebaseConfig);
const db = firebase.firestore();
const auth = firebase.auth();
const ui = new firebaseui.auth.AuthUI(auth);

// enable offline data
db.enablePersistence(`synchronizeTabs: true`);

// login ui
const uiConfig = {
  callbacks: {
    signInSuccessWithAuthResult: function (authResult, redirectUrl) {
      // User successfully signed in.
      // Return type determines whether we continue the redirect automatically
      // or whether we leave that to developer to handle.
      document.getElementById("firebaseui-auth-container").style.display = "none";
      return true;
    },
  },
  signInFlow: "popup",
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    firebase.auth.FacebookAuthProvider.PROVIDER_ID,
    firebase.auth.EmailAuthProvider.PROVIDER_ID,
  ],
};

// Indicates that the state will be persisted even when the browser window is closed
auth.setPersistence(firebase.auth.Auth.Persistence.LOCAL);

export let profileImg;
export let displayName;
export let userUid;

if (!window.localStorage.getItem("isGuest")) {
  auth.onAuthStateChanged((users) => {
    if (users) {
      // signed-in
      console.log("signed-in");
      window.localStorage.setItem("expectToBeSignedIn", true);

      // get user pic & name
      profileImg = auth.currentUser.photoURL;
      displayName = auth.currentUser.displayName;
      const email = auth.currentUser.email;

      userUid = auth.currentUser.uid;
      const userId = `${email}|${userUid}`;

      // check if data exists
      const docRef = db.collection("users").doc(userId);
      docRef
        .get()
        .then((doc) => {
          if (doc.exists) {
            // data exists
            console.log("user data exists");
            userData = JSON.parse(doc.data().userData);
            if (doc.data().addedByme) {
              addedByUsers = JSON.parse(doc.data().addedByme);
              data.push(...addedByUsers);
            }
            window.history.replaceState("", "", "/");
            updateRenderState([<MainPage key={Math.random() * 100} doAnimation={true} />, <NavicationBar key={Math.random() * 100} />]);
          } else {
            // No data
            console.log("user data doesnt exists");
            updateRenderState(
              <Suspense fallback={<Loading />}>
                <Welcome key={Math.random() * 100} skipLogIn={true} />
              </Suspense>
            );
            ui.start("#firebaseui-auth-container", uiConfig);
          }
        })
        .catch((error) => console.log("Error getting document:", error));
    } else {
      // signed-out
      console.log("signed-out");
      window.localStorage.removeItem("expectToBeSignedIn");
      updateRenderState(
        <Suspense fallback={<Loading />}>
          <Welcome key={Math.random() * 100} skipLogIn={false} />
        </Suspense>
      );
      ui.start("#firebaseui-auth-container", uiConfig);
    }
  });
}

//  signOut function
export function signOut() {
  if (window.localStorage.getItem("isGuest")) {
    window.history.back();
    window.localStorage.clear();
    window.location.reload();
  } else {
    auth
      .signOut()
      .then(() => {
        window.history.back();
        window.localStorage.clear();
        window.location.reload();
      })
      .catch((error) => {
        console.log(error);
      });
  }
}

export function storeData() {
  if (!window.localStorage.getItem("isGuest")) {
    const userUid = auth.currentUser.uid;
    const email = auth.currentUser.providerData.map((e) => e.email)[0];
    const userId = `${email}|${userUid}`;

    // remove days if more than 30
    const diaryLength = userData.diary.length;
    diaryLength > 30 && userData.diary.splice(0, diaryLength - 30);

    db.collection("users")
      .doc(userId)
      .set({ userData: JSON.stringify(userData), addedByme: JSON.stringify(addedByUsers) });
  } else {
    window.localStorage.setItem("userData", JSON.stringify(userData));
    window.localStorage.setItem("addedByUsers", JSON.stringify(addedByUsers));
  }
}

export function uploadCreatedFood(name, set) {
  db.collection("addedByUsers").doc(name).set(set);
}
