import React, { useState, useCallback, useLayoutEffect } from 'react';
import '../Styles/Measures.css';
import { wait, introPageFadeTransition } from '../dependencies';
import { requestFrame, S } from 'selector_dom';
import { userData } from '../Data';

export default function Measures(props) {
  const HarrisBenedictBMR = () => {
    const gender = userData.userInfo.gender;
    const weight = userData.userInfo.weight;
    const height = userData.userInfo.height;
    const age = userData.userInfo.age;
    const bmr =
      gender === 'male'
        ? 66.47 + 13.75 * weight + 5.003 * height - 6.755 * age
        : 655.1 + 9.563 * weight + 1.85 * height - 4.676 * age;
    return Math.round(bmr);
  };

  const ibw = eq => {
    const gender = userData.userInfo.gender;
    const height = userData.userInfo.height;
    let ibw;
    switch (eq) {
      case 'broca':
        ibw = gender === 'male' ? height - 100 - (height - 100) * 0.1 : height - 100 + (height - 100) * 0.15;
        break;
      case 'devine':
        ibw = gender === 'male' ? 50 + (height / 2.54 - 60) * 2.3 : 45.5 + (height / 2.54 - 60) * 2.3;
        break;
      case 'robinson':
        ibw = gender === 'male' ? 52 + (height / 2.54 - 60) * 1.9 : 49 + (height / 2.54 - 60) * 1.7;
        break;
      case 'millier':
        ibw = gender === 'male' ? 56.2 + (height / 2.54 - 60) * 1.41 : 53.1 + (height / 2.54 - 60) * 1.36;
        break;
      case 'hamwi':
        ibw = gender === 'male' ? 48 + (height / 2.54 - 60) * 2.7 : 45.5 + (height / 2.54 - 60) * 2.2;
        break;
      case 'lemmens':
        ibw = 22 * Math.pow(height / 100, 2);
        break;

      default:
        break;
    }
    return Math.round(ibw);
  };

  const lbm = eq => {
    const gender = userData.userInfo.gender;
    const height = userData.userInfo.height;
    const weight = userData.userInfo.weight;
    let lbm;
    switch (eq) {
      case 'boer':
        lbm = gender === 'male' ? 0.407 * weight + 0.267 * height - 19.2 : 0.252 * weight + 0.473 * height - 48.3;
        break;
      case 'james':
        lbm =
          gender === 'male'
            ? 1.1 * weight - 128 * Math.pow(weight / height, 2)
            : 1.07 * weight - 148 * Math.pow(weight / height, 2);
        break;
      case 'hume':
        lbm = gender === 'male' ? 0.3281 * weight + 0.33929 * height - 29.5336 : 0.29569 * weight + 0.41813 * height - 43.2933;
        break;

      default:
        break;
    }
    const lbmP = Math.round((lbm * 100) / weight);
    const lbmF = Math.round(100 - lbmP);
    return [Math.round(lbm), lbmP, lbmF];
  };

  const [stateBmr, setBmr] = useState(HarrisBenedictBMR());
  const [stateIbw, setIbw] = useState(ibw('broca'));
  const [stateLbm, setLbm] = useState(lbm('boer'));

  const bmiArrowAnim = useCallback(() => {
    const bmiResults = bmi();
    const arrow = S('#bmiArrow');
    if (bmiResults > 35) {
      requestFrame({ to: [100, 7], duration: 1000, delay: 300, easingFunction: 'easeOutQuint' }, (x, y) =>
        arrow.css({ left: `calc(${x}% - ${y}px)` })
      );
    } else if (bmiResults < 0) {
      arrow.style.left = 'calc(0% - 7px)';
    } else {
      const bmibar = Math.round((bmiResults * 100) / 35);
      requestFrame({ to: [bmibar, 7], duration: 1000, delay: 300, easingFunction: 'easeOutQuint' }, (x, y) =>
        arrow.css({ left: `calc(${x}% - ${y}px)` })
      );
    }
  }, []);

  useLayoutEffect(() => {
    window.scrollTo({ top: 0 });
    introPageFadeTransition('measuresPage');
    bmiArrowAnim();
    forMoreInfoAnimation();

    centerSelect('bmrSelect');
    centerSelect('ibwSelect');
    centerSelect('lbmSelect');
  }, [bmiArrowAnim]);

  const bmi = () => userData.userInfo.weight / Math.pow(userData.userInfo.height / 100, 2);

  const bmiRange = () => {
    const bmiResults = bmi();
    const age = userData.userInfo.age;
    let range, goal, color;

    if (bmiResults < 18.5 && age >= 20) {
      range = ' ';
      goal = '   ';
      color = '#d0a50b';
    } else if (bmiResults >= 18.5 && bmiResults < 24.9 && age >= 20) {
      range = ' ';
      goal = '   ';
      color = '#40ff45';
    } else if (bmiResults >= 24.9 && bmiResults < 29.9 && age >= 20) {
      range = ' ';
      goal = '   ';
      color = '#d0a50b';
    } else if (bmiResults >= 29.9 && age >= 20) {
      range = '';
      goal = '  ';
      color = '#dc2900';
    } else if (age <= 18) {
      range = '  ';
    }

    return [range, goal, color];
  };

  const MifflinStJeorBMR = () => {
    const gender = userData.userInfo.gender;
    const weight = userData.userInfo.weight;
    const height = userData.userInfo.height;
    const age = userData.userInfo.age;
    const bmr = gender === 'male' ? 10 * weight + 6.25 * height - 5 * age + 5 : 10 * weight + 6.25 * height - 5 * age - 161;
    return Math.round(bmr);
  };

  const activityMultipier = () => Math.round(stateBmr * userData.userInfo.activity);

  const tbw = () => {
    const gender = userData.userInfo.gender;
    const weight = userData.userInfo.weight;
    const height = userData.userInfo.height;
    const age = userData.userInfo.age;
    const tbw =
      gender === 'male' ? 2.447 - 0.09156 * age + 0.1074 * height + 0.3362 * weight : -2.097 + 0.1069 * height + 0.2466 * weight;
    return Math.round(tbw);
  };

  const centerSelect = async id => {
    const element = document.getElementById(id);
    const newDiv = document.createElement('p');
    newDiv.classList.add('centerSelect');
    newDiv.innerHTML = element.options[element.selectedIndex].innerHTML;
    document.body.appendChild(newDiv);
    const getWidth = parseFloat(window.getComputedStyle(newDiv).getPropertyValue('width')) / 2 + 'px)';
    newDiv.remove();

    element.style.width = '0px';
    await wait(20);
    element.style.removeProperty('width');
    element.style.paddingLeft = 'calc(50% - ' + getWidth;
  };

  const infoHandle = cardName => {
    const el = S(`#${cardName}Card`);
    let halfRotate = false;
    requestFrame({ to: 180, duration: 600, easingFunction: 'easeOutBack' }, x => {
      el.css({ transform: `perspective(700px) rotateY(${x}deg)` });
      if (x >= 90 && !halfRotate) {
        halfRotate = true;
        S(`#${cardName}Card > *`).css({ display: 'none' });
        S(`#${cardName}Card > .measureInfoClose`).css({ display: 'block' });
        S(`#${cardName}Card > .infoPara`).css({ display: 'block' });
      }
    });
  };

  const infoClosehandle = cardName => {
    const el = S(`#${cardName}Card`);
    let halfRotate = false;
    requestFrame({ from: 180, to: 360, duration: 600, easingFunction: 'easeOutBack' }, x => {
      el.css({ transform: `perspective(700px) rotateY(${x}deg)` });
      if (x >= 270 && !halfRotate) {
        halfRotate = true;
        S(`#${cardName}Card > *`).removeAttr('style');
        S(`#${cardName}Card > .measureInfoClose`).removeAttr('style');
        S(`#${cardName}Card > .infoPara`).removeAttr('style');
      }
    });
  };

  const forMoreInfoAnimation = () => {
    const getOriginalWidth = S('#forMoreInfo').getStyle('width', true);
    const getWidth = window.innerWidth <= 400 ? 160 : 180;
    const container = S('#forMoreInfo');
    const info = S('#bmiCard > .measureInfo');
    const op = { duration: 400, easingFunction: 'easeOutCirc' };

    requestFrame({ to: 1, delay: 1500, ...op }, x => {
      info.css({ fill: 'white' });
      container.css({ transform: `scale(${x})` });
    });
    requestFrame({ from: getOriginalWidth, to: getWidth, delay: 2000, ...op }, x => container.css({ width: x + 'px' }));
    requestFrame({ from: getWidth, to: getOriginalWidth, delay: 4000, ...op }, x => container.css({ width: x + 'px' }));
    requestFrame({ from: 1, to: 0, delay: 4700, ...op }, x => {
      info.removeCss("fill");
      container.css({ transform: `scale(${x})` });
    });
  };

  return (
    <div id='measuresPage'>
      <header id='measuresPageHeader'>
        <p id='measuresPageHeaderTitle'>  </p>
      </header>
      <div className='cardContainer' id='bmiCard'>
        <div id='forMoreInfo'>
          <p id='forMoreInfoPara'>  </p>
        </div>

        <svg className='measureInfo' onClick={() => infoHandle('bmi')} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
          <path d='M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <svg
          className='measureInfoClose'
          onClick={() => infoClosehandle('bmi')}
          xmlns='http://www.w3.org/2000/svg'
          viewBox='0 0 24 24'
        >
          <path d='M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <p className='mainTitles'>   ( BMI )</p>
        <p className='subTitles'>  </p>
        <p className='results'>{bmi().toFixed(1)}</p>
        <p className='subTitles' style={{ color: bmiRange()[2] }}>
          {bmiRange()[0]}
        </p>
        <div id='bmibar'>
          <span id='bmiArrow'></span>
        </div>
        <p className='subTitles'>{bmiRange()[1]}</p>
        <p className='infoPara'>
          <span>  </span>
                              
                   .             
            .                   .
        </p>
      </div>
      <div className='cardContainer' id='bmrCard'>
        <svg className='measureInfo' onClick={() => infoHandle('bmr')} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
          <path d='M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <svg
          className='measureInfoClose'
          onClick={() => infoClosehandle('bmr')}
          xmlns='http://www.w3.org/2000/svg'
          viewBox='0 0 24 24'
        >
          <path d='M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <p className='mainTitles'>  ( BMR )</p>
        <div className='selectContainer'>
          <p className='selectTitle'> </p>
          <select
            id='bmrSelect'
            className='selectEquation'
            onChange={e => {
              e.target.value === 'Harris' ? setBmr(HarrisBenedictBMR()) : setBmr(MifflinStJeorBMR());
              centerSelect('bmrSelect');
            }}
          >
            <option value='Harris'>Harris Benedict Equation</option>
            <option value='Mifflin'>Mifflin-St Jeor Equation</option>
          </select>
        </div>
        <p className='subTitles'>  </p>
        <p className='results'>
          {stateBmr}
          <span className='resultsUnits'>  </span>
        </p>
        <p className='subTitles'>   </p>
        <p className='results'>
          {activityMultipier()}
          <span className='resultsUnits'>  </span>
        </p>
        <p className='infoPara'>
          <span>  </span>
                       ()      
                               
                 ( 28  ).         
                 .
        </p>
      </div>
      <div className='cardContainer' id='ibwCard'>
        <svg className='measureInfo' onClick={() => infoHandle('ibw')} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
          <path d='M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <svg
          className='measureInfoClose'
          onClick={() => infoClosehandle('ibw')}
          xmlns='http://www.w3.org/2000/svg'
          viewBox='0 0 24 24'
        >
          <path d='M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <p className='mainTitles'>   ( IBW )</p>
        <div className='selectContainer'>
          <p className='selectTitle'> </p>
          <select
            id='ibwSelect'
            className='selectEquation'
            onChange={e => {
              setIbw(ibw(e.target.value));
              centerSelect('ibwSelect');
            }}
          >
            <option value='broca'>The Borca Index</option>
            <option value='devine'>The Davine Formula</option>
            <option value='robinson'>Robinson Formula</option>
            <option value='millier'>The Miller Formula</option>
            <option value='hamwi'>The Hamwi Formula</option>
            <option value='lemmens'>Lemmens Formula</option>
          </select>
        </div>
        <p className='subTitles'>  </p>
        <p className='results'>
          {stateIbw}
          <span className='resultsUnits'></span>
        </p>
        <p className='infoPara'>
          <span>  </span>
           (IBW)        .   (IBW)       
                  .    (IBW)      
                  .   (IBW)   .      
              .              
                (IBW).      (IBW)         
                    .      (IBW).    
           .
        </p>
      </div>
      <div className='cardContainer' id='lbmCard'>
        <svg className='measureInfo' onClick={() => infoHandle('lbm')} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
          <path d='M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <svg
          className='measureInfoClose'
          onClick={() => infoClosehandle('lbm')}
          xmlns='http://www.w3.org/2000/svg'
          viewBox='0 0 24 24'
        >
          <path d='M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <p className='mainTitles'>   ( LBM )</p>
        <div className='selectContainer'>
          <p className='selectTitle'> </p>
          <select
            id='lbmSelect'
            className='selectEquation'
            onChange={e => {
              setLbm(lbm(e.target.value));
              centerSelect('lbmSelect');
            }}
          >
            <option value='boer'>The Boer Formula</option>
            <option value='james'>The James Formula</option>
            <option value='hume'>The Hume Formula</option>
          </select>
        </div>
        <p className='subTitles'>   </p>
        <p className='results'>
          {stateLbm[0]}
          <span className='resultsUnits'></span>
        </p>
        <p className='subTitles'>    </p>
        <p className='results'>
          {stateLbm[1]}
          <span className='resultsUnits'> % </span>
        </p>
        <p className='subTitles'>    </p>
        <p className='results'>
          {stateLbm[2]}
          <span className='resultsUnits'> % </span>
        </p>
        <p className='infoPara'>
          <span>   </span>
            (LBM)              .   
              .
        </p>
      </div>
      <div className='cardContainer' id='tbwCard'>
        <svg className='measureInfo' onClick={() => infoHandle('tbw')} xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
          <path d='M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <svg
          className='measureInfoClose'
          onClick={() => infoClosehandle('tbw')}
          xmlns='http://www.w3.org/2000/svg'
          viewBox='0 0 24 24'
        >
          <path d='M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z' />
        </svg>
        <p className='mainTitles'>   ( TBW )</p>
        <p className='subTitles'>    </p>
        <p className='results'>
          {tbw()}
          <span className='resultsUnits'></span>
        </p>
        <p className='infoPara'>
          <span>    </span>
                         .     
                1980.
        </p>
      </div>
    </div>
  );
}
